import { createStore, applyMiddleware } from 'redux'
import promise from 'redux-promise-middleware'
import { combineReducers } from 'redux'
import { logger } from 'redux-logger'

import { drawerReducer } from '../app/containers/Drawer/reducer'
import { wordPageReducer } from '../app/containers/WordPage/reducer'


export const rootReducer = combineReducers({
	drawerReducer,
	wordPageReducer
})

export const store = applyMiddleware(logger, promise())(createStore)