import { 
	GET_WORDS_PENDING,
	GET_WORDS_FULFILLED,
	GET_WORDS_REJECTED,
	SET_WORD_TO_DISPLAY,
	CHANGE_BACKGROUND_COLOR,
	DRAWER_ON,
	DRAWER_OFF,
 } from './constants'


const initialState = {
	wordToDisplay: ``,
	getWords: 1,
	getWordsLoading: false,
	getWordsSuccess: false,
	getWordsError: false,
	backgroundColor: 'yellowgreen',
	drawerHidden: false
}

export const wordPageReducer = (state = initialState, action:any) => {
	switch(action.type) {
	case GET_WORDS_PENDING:
		return {
			...state, 
			getWordsLoading: true,
		}
	case GET_WORDS_FULFILLED:
		return {
			...state, 
			getWordsLoading: false,
			getWordsSuccess: true,
			getWords: action.payload,
		}
	case GET_WORDS_REJECTED:
		return {
			...state, 
			getWordsLoading: false,
			getWordsSuccess: false,
			getWordsError: true,
		}	
	case SET_WORD_TO_DISPLAY:
		return {
			...state, 
			wordToDisplay: action.payload
		}	
	case CHANGE_BACKGROUND_COLOR:
		return {
			...state, 
			backgroundColor: action.payload
		}
	case DRAWER_ON:
		return {
			...state, 
			drawerHidden: action.payload
		}
	case DRAWER_OFF:
		return {
			...state, 
			drawerHidden: action.payload
		}

	default: 
		return state
	}
}