import * as React from "react";

import './style.scss'

interface Props {
    drawerSlider: () => void
}

export const HamburgerIcon = (props: Props) => {

    return (
        <div className='hamburger-icon' onClick={props.drawerSlider}>
            <div className="hamburger-line"></div>
            <div className="hamburger-line"></div>
            <div className="hamburger-line"></div>
        </div>
    )
}