import { 
	GET_WORDS,
	SET_WORD_TO_DISPLAY,
	CHANGE_BACKGROUND_COLOR,
	DRAWER_ON,
	DRAWER_OFF,
} from './constants'

import { WordsService } from '../../../services/WordsService'


export const fetchWords = (num:number) => {
	return {
		type: GET_WORDS,
		payload: WordsService.getWords(num)
	}
}

export const changeBackgroundColor = (color: string) => {
	return {
		type: CHANGE_BACKGROUND_COLOR,
		payload: color
	}
}

export const setDisplayWord = (word:string) => {
	return {
		type: SET_WORD_TO_DISPLAY,
		payload: word
	}
}

export const drawerOn = () => {
	return {
		type: DRAWER_ON,
		payload: false
	}
}

export const drawerOff = () => {
	return {
		type: DRAWER_OFF,
		payload: true
	}
}