import * as React from 'react';
import { connect } from 'react-redux'

import { 
    fetchWords, 
    setDisplayWord, 
    changeBackgroundColor, 
    drawerOn, 
    drawerOff 
} from './actions'

import { Props, State } from './types'

import { Loading } from './../../components/Loading'
import { Error } from './../../components/Error'
import { HamburgerIcon } from './../../components/HamburgerIcon'
import Drawer  from './../Drawer'
 
import { WordDisplay } from '../../components/WordDisplay'
import { getRandomFromArray } from './../../../utils/helpers'
import { BACKGROUND_COLORS } from './../../../utils/constants'

import "./style.scss";
import { wordPageReducer } from './reducer';


class WordPage extends React.Component <Props, State> {

    componentDidMount() {
        this.getWords()
    }

    displayWord = () => {
        const { wordToDisplay, getWordsError, getWordsSuccess, words } = this.props
        if (getWordsSuccess) {
            return <WordDisplay word={wordToDisplay} changeWord={this.changeWord} backgroundColor={this.props.backgroundColor} removeDrawer={this.removeDrawer}/>
        } else if (getWordsError) {
            return <Error />
        } else if (words.length === 0) {
            return <Loading />
        }
    }

    changeWord = () => {
        const { words, changeWord, changeBackgroundColor } = this.props

        if(words.length <= 2) {
            this.getWords()
        }

        const word = getRandomFromArray(words)
        let position = words.indexOf(word)
        words.splice(position, 1)
        changeWord(word.word);
        changeBackgroundColor(getRandomFromArray(BACKGROUND_COLORS))        
    } 

    getWords = () => {
        const { selectValue, getWords } = this.props         
        getWords(selectValue)
            .then(() => {
                const { words, wordToDisplay, changeWord } = this.props
                if(!wordToDisplay || wordToDisplay.length !== selectValue) {
                    changeWord(words[0].word)
                    words.splice(0, 1)
                }        
            })
    }

    drawerSlide = () => {
        const { drawerHidden, drawerOn, drawerOff } = this.props
        if(drawerHidden) {
            drawerOn()
        } else {          
            drawerOff() 
        }        
    } 
    
   removeDrawer = () => {
        const { drawerHidden, drawerOff } = this.props
        if(!drawerHidden) {
            drawerOff()
        }
   }
    
    render () {
        return (
            <div>
                <Drawer display={this.props.drawerHidden} getWords={this.getWords} removeDrawer={this.removeDrawer}/>
                <HamburgerIcon drawerSlider={this.drawerSlide} />
                {this.displayWord()} 
            </div>
        )
    }
}

const mapStateToProps = (state: any) => {
    const { selectValue } = state.drawerReducer
    const { getWords, getWordsLoading, getWordsSuccess, getWordsError, wordToDisplay, backgroundColor, drawerHidden } = state.wordPageReducer
    
	return {
        selectValue,
        words:getWords,
        getWordsLoading,
        getWordsSuccess,
        getWordsError,
        wordToDisplay,
        backgroundColor,
        drawerHidden
    }
}

const mapDispatchToProps = (dispatch:any) => {
	return {
        getWords: (num:number) => dispatch(fetchWords(num)),
        changeWord: (word:string) => dispatch(setDisplayWord(word)),
        changeBackgroundColor: (color:string) => dispatch(changeBackgroundColor(color)),
        drawerOn: () => dispatch(drawerOn()),
        drawerOff: () => dispatch(drawerOff()),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(WordPage)

