import * as React from 'react';
import { connect } from 'react-redux'

import { changeSelectValue } from './actions'

import { Props, State } from './types'

import "./style.scss";


class Drawer extends React.Component <Props, State> {

    displayDrawer = () => {
        return this.props.display ? "drawer drawer-slide-out" : "drawer drawer-slide-in"
    }

    handleClick = (e:any) => {
        const dataValue = e.target.getAttribute('data-value')
        const { changeSelectValue, getWords, removeDrawer } = this.props
        changeSelectValue(dataValue)
        removeDrawer()
        this.forceUpdate(getWords)
    }  

    render () { 
        return (
            <div className={this.displayDrawer()}>
                    <header>
                        <div className="logo">
                            <h1>W</h1>
                        </div>
                    </header>               
                    <div className="length-selector-holder">
                      <h2>Select length</h2>
                      <ul onClick={this.handleClick}>
                          <li><div data-value={1}>One Letter</div></li>
                          <li><div data-value={2}>Two Letters</div></li>
                          <li><div data-value={3}>Three Letters</div></li>
                          <li><div data-value={4}>Four Letters</div></li>
                          <li><div data-value={5}>Five Letters</div></li>
                          <li><div data-value={6}>Six Letters</div></li>
                          {/* <li><div data-value={7}>Over Six Letters</div></li> */}
                           {/* <li>Random</li>   */}
                      </ul>
                    </div>
            </div>
        )
    }
}

const mapStateToProps = (state: any) => {
    const { selectValue } = state.drawerReducer
    
	return {
        selectValue
    }
}

const mapDispatchToProps = (dispatch:any) => {
	return {
        changeSelectValue: (val: number) => dispatch(changeSelectValue(val))
	}
}

export default connect(mapStateToProps, mapDispatchToProps)(Drawer)