import * as React from "react";

import { Props } from './types'

import './style.scss'


export const WordDisplay = (props:Props) => {

    const handleClick = () => {
        props.changeWord()
        props.removeDrawer()
    }

    return (
        <div className="word-holder words-uppercase" onClick={handleClick} style={{backgroundColor: props.backgroundColor}}>
           <h1>{props.word}</h1> 
        </div>
    )
}