import { 
	CHANGE_SELECT_VALUE
 } from './constants'


const initialState = {
	selectValue: '2'
}

export const drawerReducer = (state = initialState, action:any) => {
	switch(action.type) {
	case CHANGE_SELECT_VALUE:
		return {
			...state, 
			selectValue: action.payload
		}		
	default: 
		return state
	}
}