const postOptions =  {
    method: 'POST',
    headers : {'Content-Type': 'application/json'},
}

export const createPostOptions = (bodyToSend: any) => {
    
    const body = JSON.stringify({nol: bodyToSend})
    const postOptions: any = {
        method: 'POST',
        headers: {'Content-Type': 'application/json'},
        body: body
    }    

    return postOptions
}